.footer {
  background-color: #f2f2f2;
}

.footer__logo {
  text-align: center;
  margin-bottom: 32px;
}

.footer__social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #d9d9d9;
  border-top: 1px solid #d9d9d9;
  margin-bottom: 28px;
}

.footer__developer-link {
  text-decoration: none;
  color: #444444;
}

.footer__developer-title {
  font-family: "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}
